var SearchUtils = {};

SearchUtils.getJson = function(data) {
    if (angular.isString(data)) {
        data = data != "" ? JSON.parse(data) : {};
    }
    return data;
};

SearchUtils.normalizeObject = function(obj, removeEmpty) {
    var keys = Object.keys(obj);
    for (var i = 0; i < keys.length; i++) {
        if (obj[keys[i]] == null || removeEmpty && (obj[keys[i]] == "" || obj[keys[i]] == [])) {
            delete obj[keys[i]];
        }
    }
    return obj;
};

SearchUtils.getLicenseManager = function($scope, $sce) {
    var msg = "";
    if ($scope.license == 'expired') {
        msg = 'LICENSE EXPIRED';
    } else if ($scope.license == 'none') {
        msg = 'NO LICENSE';
    } else if ($scope.license != 'active') {
        msg = 'LICENSE ERROR';
    }

    if (msg != "") {
        msg = '<span class="red">' + msg + '</span>';
    }

    return {
        isActive: $scope.license == 'active',
        message: $sce.trustAsHtml(msg)
    };
};
