//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with 
//each doing the same thing just structuring the functions/data differently.
app.service('columnService', ['$q', '$http', 'dataService', function ($q, $http, dataService) {
    var DATA_PROPERTY_COLUMNS = 'columns';

    // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
    this.data = {
        allColumns: [],
        currentColumns: []
    };

    this.getAllColumnsGrouped = function() {
        var columns = [];
        this.data.allColumns.forEach(function(column) {
            if (column._global) {
                column._group = 'System Columns';
            } else {
                column._group = 'User Columns';
            }
            columns.push(column);
        });
        return columns;
    };

    this.getColumnIds = function(columns) {
        var result = [];
        columns.forEach(function(column){
            result.push(column.id);
        });
        return result;
    };

    this.resolveCurrentColumns = function(options, deferred) {
        // in order of query params
        options.columnIds.forEach(function(columnId) {
            this.data.allColumns.forEach(function (column) {
                if (column.id == columnId) {
                    this.data.currentColumns.push(column);
                    return;
                }
            }, this);
        }, this);
        deferred.resolve(this.data.currentColumns);
    };

    this.getColumnsBak = function(options) {
        var deferred = $q.defer();
        var columnService = this;
        this.data.currentColumns.splice(0, this.data.currentColumns.length); // reset
        if (this.data.allColumns.length == 0) {
            $http.get('/api/columns/').success(function(data) {
                [].push.apply(columnService.data.allColumns, data);
                columnService.resolveCurrentColumns(options, deferred);
            });
        } else {
            columnService.resolveCurrentColumns(options, deferred);
        }
        return deferred.promise;
    };

    this.getColumns = function(options) {
        var deferred = $q.defer();
        var columnService = this;
        this.data.currentColumns.splice(0, this.data.currentColumns.length); // reset
        if (this.data.allColumns.length == 0) {
            dataService.getProperty(DATA_PROPERTY_COLUMNS, []).then(function(data) {
                [].push.apply(columnService.data.allColumns, data);
                columnService.resolveCurrentColumns(options, deferred);
            });
        } else {
            columnService.resolveCurrentColumns(options, deferred);
        }
        return deferred.promise;
    };

    this.addColumnBak = function(column) {
        var deferred = $q.defer();
        var columnService = this;
        $http.post('/api/columns/', column).success(function(newColumn) {
            columnService.data.allColumns.push(newColumn);
            columnService.data.currentColumns.push(newColumn);
            deferred.resolve(newColumn);
        });
        return deferred.promise;
    };

    this.addColumn = function(column) {
        var deferred = $q.defer();
        var columnService = this;
        dataService.getProperty(DATA_PROPERTY_COLUMNS, []).then(function(columns) {
            var last = columns.length > 0 ? columns[columns.length - 1] : {id: 0};
            var newColumn = angular.copy(column);
            newColumn.id = last.id + 1;
            columns.push(newColumn);
            dataService.setProperty(DATA_PROPERTY_COLUMNS, columns).then(function() {
                columnService.data.allColumns.push(newColumn);
                columnService.data.currentColumns.push(newColumn);
                deferred.resolve(newColumn);
            });

        });
        return deferred.promise;
    };

    this.updateColumnBak = function(column, replacedColumn) {
        var deferred = $q.defer();
        var columnService = this;
        $http.put('/api/columns/' + column.id, column).success(function(updatedColumn) {
            // update all columns
            columnService.data.allColumns.forEach(function(column, index) {
              if (updatedColumn.id == column.id) {
                this.data.allColumns.splice(index, 1, updatedColumn); // apply changes to view
                return;
              }
            }, columnService);
            // update view
            if (angular.isDefined(replacedColumn.id)) {
                if (updatedColumn.id != replacedColumn.id) {
                    // remove duplicate first
                    var index = columnService.indexById(updatedColumn.id, columnService.data.currentColumns);
                    if (index != -1) {
                        columnService.data.currentColumns.splice(index, 1);
                    }
                }
                var index = columnService.indexById(replacedColumn.id, columnService.data.currentColumns);
                columnService.data.currentColumns.splice(index, 1, updatedColumn); // repalce column
            } else {
                var index = columnService.indexById(updatedColumn.id, columnService.data.currentColumns);
                if (index != -1) { // update existing
                    columnService.data.currentColumns.splice(index, 1, updatedColumn); // apply changes
                } else { // add
                    columnService.data.currentColumns.splice(columnService.data.currentColumns.length, 0, updatedColumn);
                }
            }
            deferred.resolve(columnService.data.currentColumns);
        });
        return deferred.promise;
    };

    this.updateColumn = function(column, replacedColumn) {
        var deferred = $q.defer();
        var columnService = this;
        dataService.getProperty(DATA_PROPERTY_COLUMNS, []).then(function(columns) {
            var i = columnService.indexById(column.id, columns);
            var updatedColumn = angular.copy(column);
            columns[i] = updatedColumn;
            dataService.setProperty(DATA_PROPERTY_COLUMNS, columns).then(function() {
                var j = columnService.indexById(column.id, columnService.data.allColumns);
                columnService.data.allColumns.splice(j, 1, updatedColumn); // apply changes to view
                var k = columnService.indexById(column.id, columnService.data.currentColumns);
                columnService.data.currentColumns.splice(k, 1, updatedColumn); // apply changes to view
            });
        });
        // FIXME: replacedColumn?
        return deferred.promise;
    };

    this.removeColumnBak = function(column, destroy) {
        var deferred = $q.defer();
        if (destroy) {
            $http.delete('/api/columns/' + column.id);
            var index = this.indexById(column.id, this.data.allColumns);
            if (index != -1) { // concurrent changes?
                this.data.allColumns.splice(index, 1);
            }
        }
        var index = this.indexById(column.id, this.data.currentColumns);
        if (index != -1) {
            this.data.currentColumns.splice(index, 1); // remove
        }
        deferred.resolve(this.data.currentColumns);
        return deferred.promise;
    };

    this.removeColumn = function(column, destroy) {
        var deferred = $q.defer();
        var columnId = column.id;
        if (destroy) {
            var columnService = this;
            dataService.getProperty(DATA_PROPERTY_COLUMNS, []).then(function(columns) {
                var i = columnService.indexById(columnId, columns);
                columns.splice(i, 1);
                dataService.setProperty(DATA_PROPERTY_COLUMNS, columns).then(function() {
                    var j = columnService.indexById(columnId, columnService.data.allColumns);
                    columnService.data.allColumns.splice(j, 1); // apply changes to view
                });
            });
        }
        var index = this.indexById(columnId, this.data.currentColumns);
        if (index != -1) {
            this.data.currentColumns.splice(index, 1); // remove
        }
        deferred.resolve(this.data.currentColumns);
        return deferred.promise;
    };

    this.indexById = function(id, array) {
        var index = -1;
        array.forEach(function(object, i) {
            if (id == object.id) {
                index = i;
                return;
            }
        });
        return index;
    };

    this.findById = function(id, array) {
        var index = this.indexById(id, array);
        if (index != -1) {
            return array[index];
        } else {
            return null;
        }
    };

    this.resetState = function() {
        this.data.allColumns.splice(0, this.data.allColumns.length); // reset
    };
}]);
