app.directive('context', function() {
    return {
        restrict: 'E',
        scope: {
            items: "="
        },
        link: function(scope, element, attrs) {
            var context = scope.items;
            angular.extend(scope.$parent, context);
        }
    }
});

app.directive('issueColumn', ['$compile', function($compile) {
    return {
        restrict: 'E',
        scope: {
            issue: '=',
            column: '='
        },
        link: function(scope, element, attrs) {
            element.html($compile(scope.column.template)(scope));
        }
    }
}]);

app.directive('issueColumn2', ['$compile', function($compile) {
    return {
        restrict: 'E',
        scope: {
            issue: '=',
            column: '=',
            isValid: '='
        },
        link: function(scope, element, attrs) {
            scope.$watchCollection(attrs.column, function() {
                if (scope.isValid) {
                    element.html($compile(scope.column.template)(scope));
                } else {
                    element.html('');
                }
            });
        }
    }
}]);

