// https://docs.atlassian.com/aui/latest/docs/auiselect2.html
// TODO: ui-select with aui theme
// see also https://github.com/angular-ui/ui-select
// and https://bitbucket.org/atlassian/aui/src/master/src/js/aui-select2.js
app.directive('auiSelect2', ['$timeout', function ($timeout) {
    return {
        require: 'ngModel',
        restrict: 'A',
        link: function(scope, elem, attrs, ngModel) {
            scope.$watch(attrs.ngModel, function() {
                $(elem).auiSelect2();
            });
        }
    }
}]);

app.directive('columnsDropdown', ['$timeout', function ($timeout) {
    return {
        restrict: 'E',
        scope: {
            isGadget: "=",
            columns: "=",
            selected: "=",
            onClose: "&"
        },
        templateUrl: '/templates/columnsDropdown.html',
        link: function(scope, elem, attrs) {
            scope.checked = function(column) {
                return scope.selected.indexOf(column.id) >= 0 ? 'checked' : '';
            };

            scope.save = function() {
                var newIds = [];
                var columnItems = angular.element('#columns-dropdown > div > ul > li > a');
                columnItems.each(function(index, columnItem) {
                    var $columnItem = angular.element(columnItem);
                    if ($columnItem.hasClass('checked')) {
                        newIds.push($columnItem.scope().column.id);
                    }
                });
                updateIds(scope.selected, newIds);
                angular.element('#columns-dropdown-trigger').trigger('aui-button-invoke');
                scope.onClose();
            };

            // https://bitbucket.org/atlassian/aui/src/master/src/js/dropdown2.old.js
            // FIXME: close the menu when clicking on elements which aren't "interactive"
            // see https://bitbucket.org/atlassian/aui/src/master/src/js/dropdown2.js?#cl-295
            scope.cancel = function() {
                var columnItems = angular.element('#columns-dropdown > div > ul > li > a');
                columnItems.each(function(index, columnItem) {
                    var $columnItem = angular.element(columnItem);
                    var columnId = $columnItem.scope().column.id;
                    if (scope.selected.indexOf(columnId) == -1) {
                        $columnItem.removeClass('checked');
                        $columnItem.removeClass('aui-dropdown2-checked');
                    } else {
                        $columnItem.addClass('checked');
                    }
                });
                angular.element('#columns-dropdown-trigger').trigger('aui-button-invoke');
            };

            var updateIds = function(selected, newIds) {
                // remove deselected
                angular.forEach(selected, function(id, index) {
                    if (newIds.indexOf(id) == -1) {
                        selected.splice(index, 1);
                    }
                });
                // add newly selected
                angular.forEach(newIds, function(id) {
                    if (selected.indexOf(id) == -1) {
                        selected.push(id);
                    }
                });
            };
        }
    }
}]);


