app.service('dataService', ['$q', 'applicationLoggingService', '$http', function ($q, logger, $http) {
    var AP = window.RA || window.AP;
    var PROPERTIES_URL = '/rest/atlassian-connect/1/addons/lambda-plugin/properties/';

    // https://developer.atlassian.com/static/connect/docs/latest/concepts/hosted-data-storage.html

    var $migrate = $q.defer();
    var hasProperty = {};

    this.getProperty = function(name, defaultValue) {
        var deferred = $q.defer();

        $migrate.promise.then(function() {
            if (!hasProperty[name]) {
                deferred.resolve(defaultValue);
                return;
            }

            AP.request({
                url: PROPERTIES_URL + name,
                success: function(data) {
                    var property = SearchUtils.getJson(data);
                    var value = SearchUtils.getJson(property.value);
                    deferred.resolve(value);
                },
                error: function(e) {
                    deferred.resolve(defaultValue);
                }
            });
        });
        return deferred.promise;
    };

    this.setProperty = function(name, value) {
        var deferred = $q.defer();

        AP.request({
            url: PROPERTIES_URL + name,
            type: 'PUT',
            data: JSON.stringify(value),
            contentType: 'application/json',
            success: function(data) {
                var property = SearchUtils.getJson(data);
                var value = SearchUtils.getJson(property.value);
                deferred.resolve(value);
                hasProperty[name] = true;
            },
            error: function(e) {
                deferred.reject(e);
            }
        });
        return deferred.promise;
    };

    var columnId = 1;
    var createColumn = function(name, template, expression) {
        return {
            id: columnId++,
            _global: true,
            name: name,
            template: template,
            expression: expression
        };
    };

    this.createColumns = function() {
        var $deferred = $q.defer();
        var columns = [];
        columns.push(createColumn(
            'Timespent',
            '<span ng-bind="{{column.expression}}"/>',
            'issue.fields.timespent | prettyHours'));
        columns.push(createColumn(
                    'Time to resolve',
                    '<span class="{{issue | timeToResolveLozenge}}" ng-bind="{{column.expression}}"/>',
                    'issue | timeToResolve'));
        columns.push(createColumn(
                    'Time left',
                    '<span class="{{issue | timeLeftLozenge}}" ng-bind="{{column.expression}}"/>',
                    'issue | timeLeft'));
        this.setProperty('columns', columns).then($deferred.resolve, $deferred.resolve);
        return $deferred.promise;
    };

    var dataService = this;

    AP.request({
        url: PROPERTIES_URL,
        success: function(data) {
            data = SearchUtils.getJson(data);
            if (data.keys.length == 0) {
                // pre-create default columns
                dataService.createColumns().then($migrate.resolve);
            } else {
                for (var i = 0; i < data.keys.length; i++) {
                    var key = data.keys[i];
                    hasProperty[key.key] = true;
                }
                $migrate.resolve();
            }
        }
    });

}]);