var app = angular.module('searchApp', ['ngRoute', 'localization', 'aui', 'talis.services.logging', 'xeditable', "ui.sortable"]);

app.run(['editableOptions', 'editableThemes', function(editableOptions, editableThemes) {
    editableOptions.theme = 'aui';
    editableThemes.aui = {
      formTpl:      '<form class="aui"></form>',
      noformTpl:    '<span class="editable-wrap"></span>',
      controlsTpl:  '<div class="inline-edit-fields" tabindex="1"><div class="field-group"></div></div>',
      inputTpl:     '',
      errorTpl:     '<div class="editable-error" ng-show="$error" ng-bind="$error"></div>',
      buttonsTpl:   '<span class="editable-buttons"></span>',
      submitTpl:    '<button class="aui-button submit" accesskey="s" type="submit" title="Press Alt+s to submit this form">'+
                        '<span class="aui-icon aui-icon-small aui-iconfont-success">Save</span></button>',
      cancelTpl:    '<button class="aui-button cancel" type="cancel" ng-click="$form.$cancel()" title="Press Alt+` to cancel">' +
                        '<span class="aui-icon aui-icon-small aui-iconfont-close-dialog">Cancel</span></button>',

      buttonsClass: '',
      inputClass: 'text',
      postrender: function() {
        //apply `form-control` class to std inputs
        switch(this.directiveName) {
          case 'editableText':
          case 'editableSelect':
          case 'editableTextarea':
          case 'editableEmail':
          case 'editableTel':
          case 'editableNumber':
          case 'editableUrl':
          case 'editableSearch':
          case 'editableDate':
          case 'editableDatetime':
          case 'editableTime':
          case 'editableMonth':
          case 'editableWeek':
            this.inputEl.addClass('form-control');
            if(this.theme.inputClass) {
              // don`t apply `input-sm` and `input-lg` to select multiple
              // should be fixed in bs itself!
              if(this.inputEl.attr('multiple') &&
                (this.theme.inputClass === 'input-sm' || this.theme.inputClass === 'input-lg')) {
                  break;
              }
              this.inputEl.addClass(this.theme.inputClass);
            }
          break;
        }

        //apply buttonsClass (bs3 specific!)
        if(this.buttonsEl && this.theme.buttonsClass) {
          this.buttonsEl.find('button').addClass(this.theme.buttonsClass);
        }
      }
    };
}]);

app.config(['$routeProvider', '$httpProvider', '$provide',
        function ($routeProvider, $httpProvider, $provide) {
    var AP = window.RA || window.AP;

    var projectKey = ['$q', function($q) {
        var deferred = $q.defer();
        AP.getLocation(function(location){
            var result = location.split('project.key=')[1];
            result = result ? result.split('&')[0] : undefined;
            deferred.resolve(result);
        });
        return deferred.promise;
    }];

    var loggedInUser = ['$q', '$rootScope', '$location',
        function($q, $rootScope, $location) {

        var deferred = $q.defer();
        AP.getUser(function(user) {
            deferred.resolve(user);
        });
        /* error
            $rootScope.loggedInUser = undefined;
        */
        return deferred.promise;
    }];

    $routeProvider.when('/', {
            controller: 'SearchController',
            templateUrl: 'templates/main.html',
            resolve: {
                loggedInUser: loggedInUser,
                projectKey: projectKey
            }
        }).when('/columns', {
            controller: 'ColumnController',
            templateUrl: 'templates/main.html',
            resolve: {
                loggedInUser: loggedInUser,
                projectKey: projectKey
            }
        }).otherwise({redirectTo: '/'});

    $provide.decorator("$exceptionHandler", ['$delegate', 'exceptionHandlerInterceptor',
            function($delegate, exceptionHandlerInterceptor) {
        return function(exception, cause) {
            if (exceptionHandlerInterceptor.invoke) {
                exceptionHandlerInterceptor.invoke(exception, cause);
            } else {
                $delegate(exception, cause);
            }
        };
    }]);

    // authentication token
    $httpProvider.defaults.headers.common['Authorization'] = "JWT " + window.atlToken;
    $provide.factory('authHttpInterceptor', ['$injector', '$q', function($injector, $q) {

        return {
            response: function(response) {
                var xAcpt = response.headers('X-acpt');
                if (xAcpt) {
                     // update authentication token
                    $httpProvider.defaults.headers.common['X-acpt'] = xAcpt;
                    //  schedule automatic refresh before token expires
                    $injector.get('authbeat').authbeat();
                }
                return response;
            },
            'responseError': function(rejection) {
               if (rejection.status == 401 && rejection.config.url != '/authbeat') {
                   AP.require('messages', function(messages) {
                       messages.error('Authentication expired', "Reload page to restore communication with add-on backend");
                   });
               }
               return $q.reject(rejection);
             }
        }
    }]);
    $provide.factory('urlRewriteHttpInterceptor', ['$injector', '$window', function($injector, $window) {
        return {
            request: function(config) {
                var path = config.url.match(/^\/?(.+?)\//);
                if (path && path[1] == 'i18n') {
                    config.url = $window.i18nDefault;
                }
                return config;
            }
        }
    }]);
    $httpProvider.interceptors.push('authHttpInterceptor', 'urlRewriteHttpInterceptor');
}]).service('exceptionHandlerInterceptor', function () {
}).service('authbeat', ['$timeout', '$http', function ($timeout, $http) {
    this.authbeatPromise = null;
    this.authbeat = function() {
        $timeout.cancel(this.authbeatPromise);
        this.authbeatPromise = $timeout(function() {
            $http.get('/authbeat');
            // token remains valid for 15 minutes by default - configured using maxTokenAge.
        }, 14*60*1000, false);
    };
}]);
