//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with 
//each doing the same thing just structuring the functions/data differently.
app.service('searchService', ['$q', 'applicationLoggingService', function ($q, logger) {
    var AP = window.RA || window.AP;
    var DASHBOARD_PROPERTY_GADGET_CONFIG = 'gadgetConfig';

    // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
    this.data = {
        allIssues: [],
        allFilters: [],
        issues: [],
        issuesCount: {
            total: null,
            startAt: 0,
            maxResults: 50
        }
    };

    this.getIssues = function(options) {
        var deferred = $q.defer();
        if (this.data.issuesCount.total == null || this.data.filterId != options.filterId
                || (angular.isDefined(options.startAt) && this.data.issuesCount.startAt != options.startAt)) {
            delete this.data.selectedIssueId;
            this.data.issues.splice(0, this.data.issues.length);
            this.data.filterId = options.filterId;
            var searchService = this;
            this.getAllIssues(options.startAt, options).then(function(data) {
                Array.prototype.push.apply(searchService.data.issues, data.issues);
                searchService.data.issuesCount.total = data.total;
                searchService.data.issuesCount.startAt = data.startAt;
                searchService.data.issuesCount.maxResults = data.maxResults;
                deferred.resolve(searchService.data.issues);
            });
        } else {
            deferred.resolve(this.data.issues);
        }
        return deferred.promise;
    };

    this.getIssuesBak = function(options) {
        var deferred = $q.defer();
        if (this.data.allIssues.length == 0 || this.filterId != options.filterId) {
            this.data.allIssues.splice(0, this.data.allIssues.length); // reset
            this.filterId = options.filterId;
            var firstDeferred = this.getAllIssues(/* startAt */ 0, options);
            var searchService = this;
            firstDeferred.then(function(data) {
                if (data.total > data.maxResults) {
                    var deferreds = [];
                    for (var i = 0; i < /* (float) */ data.total / data.maxResults - 1; i++) {
                        var nextDeferred = service.getAllIssues(/* startAt */ data.maxResults * (i + 1), options);
                        deferreds.push(nextDeferred);                        
                    }
                    // arguments is an object: http://javascript.info/tutorial/arguments
                    // objects may have 2^32 - 1 properties: http://stackoverflow.com/questions/9282869/javascript-limits-to-number-of-object-properties
                    $q.all.apply($q, deferreds).then(function() {
                        deferred.resolve(searchService.data.allIssues);
                    });
                } else {
                    deferred.resolve(searchService.data.allIssues);
                }
            });
        } else {
            deferred.resolve(this.data.allIssues);
        }
        
        return deferred.promise;
    };

    this.getAllIssues = function(startAt, options) {
        var filterId = options.filterId;
        var projectKey = options.projectKey;
        var searchService = this;
        var maxResults = options.maxResults || 1000;
        var deferred = $q.defer();
        var queryBuilder = new this.queryBuilder()
            // fields=worklog or expand=worklog
            .addQuery('fields', '*all')
            .addQuery('startAt', startAt)
            // maxResults <= jira.search.views.default.max = 1000
            .addQuery('maxResults', maxResults);
        if (filterId) {
            // https://answers.atlassian.com/questions/22264/can-filters-be-used-with-the-rest-api
            queryBuilder.addQuery('jql', 'filter=' + filterId);
        } else if (projectKey) {
            // https://answers.atlassian.com/questions/155180/filter-issue-by-project-key
            queryBuilder.addQuery('jql', 'project="' + projectKey + '"');
        }
        var query = queryBuilder.build();
        var url = '/rest/api/2/search?' + query;
        AP.request({
            url: url,
            dataType: 'json', // does not work!
            success: function(data) {
                if (typeof data === 'string') {
                    data = JSON.parse(data);
                }
                Array.prototype.push.apply(searchService.data.allIssues, data.issues);
                deferred.resolve(data);
            },
            error: function(jqXHR) {
                var args = Array.prototype.slice.call(arguments)
                args.push(url, deferred);
                searchService.onError.apply(searchService, args);
            }
        });

        return deferred.promise;
    };

    this.getFilters = function(loggedInUser, options, initFilters) {
        var searchService = this;
        var deferred = $q.defer();

        if (searchService.data.allFilters.length == 0) {
            var url = '/rest/api/2/filter/favourite';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    if (typeof data === 'string') {
                        data = JSON.parse(data);
                    }
                    Array.prototype.push.apply(searchService.data.allFilters, data);
                    initFilters(searchService.data.allFilters);
                    deferred.resolve(searchService.data.allFilters);
                },
                error: function() {
                    var args = Array.prototype.slice.call(arguments)
                    args.push(url, deferred);
                    searchService.onError.apply(searchService, args);
                }
            });
        } else {
            deferred.resolve(searchService.data.allFilters);
        }
        return deferred.promise;
    };

    this.onError = function(jqXHR, type, error, url, deferred) {
        if (jqXHR.status == 401) {
            this.loginExpired();
        } else if (jqXHR.status == 503) {
            this.serviceUnavailable();
        } else if (jqXHR.status == 403) {
            this.forbidden();
        } else {
            this.unexpectedError(jqXHR, error, url);
        }
        deferred.reject();
    };

    this.loginExpired = function() {
        AP.require('messages', function(messages) {
            messages.error('JIRA session expired', "Please login again to restore connection with JIRA");
        });
    };

    this.serviceUnavailable = function() {
        AP.require('messages', function(messages) {
            messages.error('JIRA Unavailable', "The requested resource failed to load. If the problem persists, please contact Atlassian Cloud Support.");
        });
    };

    this.forbidden = function() {
        AP.require('messages', function(messages) {
            messages.error('Forbidden', "You need to accept WRITE and DELETE scopes for the add-on in UPM");
        });
    };

    this.unexpectedError = function(jqXHR, error, cause) {
        var body = '';
        if (jqXHR && jqXHR.responseText) {
            try {
                var data = TimesheetUtils.getJson(jqXHR.responseText);
                body = data.errorMessages.join(' ');
                logger.error(data, cause);
            } catch(err) {
                body = jqXHR.responseText;
                logger.error(jqXHR.responseText, cause);
            }
        } else {
            body = error;
            logger.error(error, cause);
        }
        AP.require('messages', function(messages) {
            messages.error('Unexpected error', body);
        });
    };

    this.getDefaultFilter = function(projectKey) {
        var defaultFilter = {id: ''};
        if (projectKey) {
            defaultFilter.name = "All " + projectKey + " Issues";
        } else {
            defaultFilter.name = "All Issues";
        }
        return defaultFilter;
    };

    this.getGadgetConfiguration = function(dashboardId, dashboardItemId) {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties',
            success: function(data) {
                data = SearchUtils.getJson(data);
                if (data.keys.length > 0) { // assume it has DASHBOARD_PROPERTY_GADGET_CONFIG
                    AP.request({
                        url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
                        success: function(data) {
                            data = SearchUtils.getJson(data);
                            deferred.resolve(data.value);
                        },
                        error: function(e) {
                            deferred.resolve({});
                        }
                    });
                } else {
                    deferred.resolve({});
                }
            },
            error: function(e) {
                deferred.resolve({});
            }
        });
        return deferred.promise;
    };

    this.saveGadgetConfiguration = function(dashboardId, dashboardItemId, configuration) {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
            type: 'PUT',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: JSON.stringify(configuration),
            success: function(data) {
                deferred.resolve(SearchUtils.getJson(data));
            },
            error: function(e){
                deferred.reject();
            }
        });
        return deferred.promise;
    };

    this.resetState = function() {
        this.data.issuesCount.total = null;
        this.data.filterId = null;
        this.data.allIssues.splice(0, this.data.allIssues.length); // reset
        this.data.allFilters.splice(0, this.data.allFilters.length); // reset
    };

    this.queryBuilder = function() {
        var items = [];

        this.addQuery = function(param, value) {
            if (value != null && value != "") {
                items.push(param + "=" + value);
            }
            return this;
        };

        this.build = function() {
            var result = "";
            for (var i = 0; i < items.length; i++) {
                if (i != 0) {
                    result += "&";
                }
                result += items[i];
            }
            return result;
        };
    };
}]);
