app.controller('ColumnDialogController', function ($scope, columnService, columns, selectedColumnIds, $modalInstance) {

    $scope.columnOptions = {selected: '', columns: columns};

    $scope.selectedColumns = [];

    selectedColumnIds.forEach(function (id) {
        var column = columnService.findById(id, columns);
        $scope.selectedColumns.push(column);
    });

    $scope.columnSelected = function() {
        var alreadyAdded = false;
        var newColumn = columnService.findById($scope.columnOptions.selected, columns);
        $scope.selectedColumns.forEach(function(column){
            if (column.id == newColumn.id) {
                alreadyAdded = true;
            }
        });
        if (alreadyAdded) {
            AP.require('messages', function(messages) {
                messages.warning("Failed to add column", "Column is already added");
            });
        }
        else {
            $scope.selectedColumns.push(newColumn);
        }
    };

    $scope.removeSelectedColumn = function(columnId) {
        for (var i = 0; i < $scope.selectedColumns.length; i++) {
            if ($scope.selectedColumns[i].id == columnId) {
                $scope.selectedColumns.splice(i, 1);
            }
        }
    };

    $scope.saveSelectedColumns = function() {
        $scope.closeDialog(columnService.getColumnIds($scope.selectedColumns));
    };

    $scope.cancelDialog = function () {
        $modalInstance.dismiss();
    };

    $scope.closeDialog = function (columns) {
        $modalInstance.close(columns);
    };
});