app.controller('ColumnController',
    ['$scope', '$routeParams', '$sce', '$q', '$route', 'searchService', 'columnService',
     '$compile', '$interpolate', 'loggedInUser', 'exceptionHandlerInterceptor', 'projectKey',
     'applicationLoggingService',
    function ($scope, $routeParams, $sce, $q, $route, searchService, columnService,
     $compile, $interpolate, loggedInUser, exceptionHandlerInterceptor, projectKey,
     logger) {

        $scope.menuInfo = {
            "contentTemplate": 'templates/columns.html',
            "isColumn": true
        };

        $scope.licenseManager = SearchUtils.getLicenseManager($scope, $sce);

        $scope.filterOptions = {
            filters: searchService.data.allFilters
        };
        var defaultFilter = searchService.getDefaultFilter(projectKey);
        searchService.getFilters(loggedInUser, {}, function(filters) {
            filters.splice(0, 0, defaultFilter); // init first time only
        }).then(function(filters) {
            $scope.filterOptions.selected = searchService.data.filterId || defaultFilter.id;
            $scope.filterSelected();
        });

        $scope.columns = columnService.data.allColumns;
        columnService.getColumns({columnIds: []}).then(function() {
            $scope.columnSelected(columnService.data.selectedColumnId || $scope.columns[0].id);
        });

        $scope.column = {};

        var columnTemplate = {
            name: '',
            template: '<span ng-bind="{{column.expression}}"/>',
            expression: ''
        };
        if (!angular.isDefined($scope.column.id)) {
            angular.copy(columnTemplate, $scope.column);
        }
        $scope.columnSelected = function(columnId) {
            columnService.data.selectedColumnId = columnId;
            var column = columnService.findById(columnId, $scope.columns);
            if (column != null) {
                angular.copy(column, $scope.column);
            }
        };

        $scope.preview = {};

        var issues = searchService.data.allIssues;
        $scope.issueOptions = {
            issues: issues,
            selected: searchService.data.selectedIssueId || (issues.length > 0 ? issues[0].id : '')
        };

        $scope.filterSelected = function() {
            searchService.getIssues({filterId: $scope.filterOptions.selected}).then(function(issues) {
                $scope.issueOptions.issues = issues;
                $scope.issueOptions.selected = searchService.data.selectedIssueId || (issues.length > 0 ? issues[0].id : null);
                $scope.issueSelected();
            });
        };

        $scope.issueSelected = function() {
            searchService.data.selectedIssueId = $scope.issueOptions.selected;
            var issue = columnService.findById($scope.issueOptions.selected, $scope.issueOptions.issues);
            var censor = function(key, value) {
                if (['expand', 'self', '$$hashKey'].indexOf(key) != -1) {
                    return undefined;
                }
                return value;
            };
            if (issue != null) {
                $scope.preview.issue = issue;
                $scope.preview.issueJson = JSON.stringify(issue, censor, '  ');
            }
        };

        $scope.columnOptions = {};
        var validateTemplate = function() {
            delete $scope.columnOptions.templateError;
            if (angular.isDefined($scope.columnOptions.templateError)) {
                return;
            }
            $scope.columnOptions.noTemplateError = true;
            exceptionHandlerInterceptor.invoke = function(exception, cause) {
                $scope.columnOptions.templateError = exception.message;
                delete $scope.columnOptions.noTemplateError;
            };
            $compile($scope.column.template)($scope); // FIXME: scope
            delete exceptionHandlerInterceptor.invoke;
        };

        $scope.$watch('column.template', validateTemplate);

        $scope.$watch('column.expression', function() {
            delete $scope.columnOptions.expressionError;
            $scope.columnOptions.noExpressionError = true;
            exceptionHandlerInterceptor.invoke = function(exception, cause) {
                $scope.columnOptions.expressionError = exception.message;
                delete $scope.columnOptions.noExpressionError;
            };
            $interpolate('{{' + $scope.column.expression + '}}')($scope); // FIXME: scope
            delete exceptionHandlerInterceptor.invoke;
            validateTemplate();
        });

        $scope.saveColumn = function (columnForm) {
            var deferred;
            if ($scope.column._global) {
                delete $scope.column.id;
                delete $scope.column._global;
            }
            if (!angular.isDefined($scope.column.id)) {
                deferred = columnService.addColumn($scope.column);
                deferred.then(function(column) {
                    $scope.columnSelected(column.id);
                });
            } else {
                deferred = columnService.updateColumn($scope.column, {});
            }
            $scope.showSaveAsNewButton = false;
            columnForm.$setPristine();
            return deferred.promise;
        };

        $scope.columnNameChanged = function () {
            if ($scope.column.name && $scope.column.id && !$scope.column._global) {
                $scope.showSaveAsNewButton = true;
            }
        };

        $scope.saveAsNewColumn = function (columnForm) {
            delete $scope.column.id;
            $scope.saveColumn(columnForm);
        };

        $scope.removeColumn = function (destroy) {
            var deferred = columnService.removeColumn($scope.column, destroy);
            $scope.columnSelected($scope.columns.length > 0 ? $scope.columns[0].id : '');
            return deferred.promise;
        };

        $scope.newColumn = function () {
            angular.copy(columnTemplate, $scope.column);
            $scope.showSaveAsNewButton = false;
        };

        $scope.refresh = function() {
            searchService.resetState();
            columnService.resetState();
            $route.reload();
        };

        $scope.resize = function() {
            AP.resize();
        };

        $scope.logger = logger;
    }
]);
