//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with 
//each doing the same thing just structuring the functions/data differently.
app.service('viewService', ['$q', '$http', 'dataService', function ($q, $http, dataService) {
    var DATA_PROPERTY_VIEWS = 'views';

    // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
    this.data = {
        allViews: [],
        currentView : {
            name: 'Untitled',
            columns: []
        }
    };

    this.updateCurrentView = function (options) {
        var viewService = this;
        viewService.data.allViews.forEach(function(view){
            if (view.id == options.viewId) {
                if (!viewService.data.currentView.id || viewService.data.currentView.id != options.viewId) {
                    angular.copy(view, viewService.data.currentView);
                    viewService.data.currentView._origColumns = view.columns;
                }
            }
        }, viewService);
        if (!viewService.data.currentView.id && viewService.data.allViews.length > 0) {
            var view = viewService.data.allViews[0];
            angular.copy(view, viewService.data.currentView);
            viewService.data.currentView._origColumns = view.columns;
        }
    };

    this.getViews = function(options) {
        var deferred = $q.defer();
        var viewService = this;
        if (this.data.allViews.length == 0) {
            dataService.getProperty(DATA_PROPERTY_VIEWS, []).then(function(data) {
                [].push.apply(viewService.data.allViews, data);
                viewService.updateCurrentView(options);
                deferred.resolve(viewService.data.allViews);
            });
        } else {
            viewService.updateCurrentView(options);
            deferred.resolve(viewService.data.allViews);
        }
        return deferred.promise;
    };

    this.addView = function(view) {
        var deferred = $q.defer();
        var viewService = this;
        dataService.getProperty(DATA_PROPERTY_VIEWS, []).then(function(views) {
            var last = views.length > 0 ? views[views.length - 1] : {id: 0};
            var newView = angular.copy(view);
            newView.id = last.id + 1;
            views.push(newView);
            dataService.setProperty(DATA_PROPERTY_VIEWS, views).then(function() {
                viewService.data.allViews.push(newView);
                angular.copy(newView, viewService.data.currentView);
                viewService.data.currentView._origColumns = newView.columns;
                deferred.resolve(viewService.data.currentView);
            });
        });
        return deferred.promise;
    };

    this.updateView = function(view) {
        var deferred = $q.defer();
        var viewService = this;
        dataService.getProperty(DATA_PROPERTY_VIEWS, []).then(function(views) {
            var i = viewService.indexById(view.id, views);
            var updatedView = angular.copy(view);
            views[i] = updatedView;
            dataService.setProperty(DATA_PROPERTY_VIEWS, views).then(function() {
                // update all views
                var index = viewService.indexById(view.id, viewService.data.allViews);
                viewService.data.allViews.splice(index, 1, updatedView); // apply changes to view
                // update view
                if (!angular.equals(view, viewService.data.currentView)) {
                    angular.copy(view, viewService.data.currentView);
                }
                deferred.resolve(viewService.data.currentView);
            });
        });
        return deferred.promise;
    };

    this.removeView = function(view) {
        var deferred = $q.defer();
        var viewService = this;
        var viewId = view.id;
        dataService.getProperty(DATA_PROPERTY_VIEWS, []).then(function(views) {
            var i = viewService.indexById(viewId, views);
            views.splice(i, 1);
            dataService.setProperty(DATA_PROPERTY_VIEWS, views).then(function() {
                var index = viewService.indexById(viewId, viewService.data.allViews);
                viewService.data.allViews.splice(index, 1);
                angular.copy({
                    name: 'Untitled',
                    columns: []
                }, viewService.data.currentView);
                deferred.resolve(viewService.data.allViews);
            });
        });
        return deferred.promise;
    };

    this.indexById = function(id, array) {
        var index = -1;
        array.forEach(function(object, i) {
            if (id == object.id) {
                index = i;
                return;
            }
        });
        return index;
    };

    this.findById = function(id, array) {
        var index = this.indexById(id, array);
        if (index != -1) {
            return array[index];
        } else {
            return null;
        }
    };

    this.resetState = function() {
        this.data.allViews.splice(0, this.data.allViews.length); // reset
    };
}]);
