app.controller('GadgetController', ['$scope', '$rootScope', '$route', '$routeParams', '$q', '$location', '$timeout', 'searchService',
    function ($scope, $rootScope, $route, $routeParams, $q, $location, $timeout, searchService) {

        var getCurrentConfiguration = function() {
            return SearchUtils.normalizeObject({
                viewId: $scope.$parent.viewOptions.selected,
                filterId: $scope.$parent.filterOptions.selected
            }, true)
        };

        $scope.saveGadgetConfig = function() {
            var configuration = getCurrentConfiguration();
            searchService.saveGadgetConfiguration($scope.dashboardId, $scope.dashboardItemId, configuration)
                .then($rootScope.$emit('gadgetConfigSave'));
        };

        $scope.cancelGadgetConfigSaving = function() {
            $rootScope.$emit('gadgetConfigCancel');
        };
    }
]);