app.controller('SearchController',
    ['$scope', '$rootScope', '$sce', '$q', '$route', '$routeParams', '$location', '$modal', 'searchService',
        'columnService', 'viewService', 'loggedInUser', 'projectKey', '$document',
    function ($scope, $rootScope, $sce, $q, $route, $routeParams, $location, $modal, searchService,
        columnService, viewService, loggedInUser, projectKey, $document) {

        if ($scope.isGadget) {
            searchService.getGadgetConfiguration($scope.dashboardId, $scope.dashboardItemId).then(function(config) {
                init(config.viewId, config.filterId || '');
            })
        } else {
            init(parseInt($routeParams.viewId), '');
        }

        function init(viewId, filterId) {

            $scope.showGadgetConfiguration = false;

            if ($scope.isGadget) {
                AP.require('jira', function (jira) {
                    jira.DashboardItem.onDashboardItemEdit(function () {
                        $scope.$apply('showGadgetConfiguration = true');
                        AP.resize();
                    });
                });
            }

            $rootScope.$on('gadgetConfigSave', function() {
                $scope.refresh();
            });

            $rootScope.$on('gadgetConfigCancel', function() {
                $scope.showGadgetConfiguration = false;
                AP.resize();
            });

            $scope.menuInfo = {
                "contentTemplate": 'templates/issues.html',
                "isView": true
            };

            $scope.licenseManager = SearchUtils.getLicenseManager($scope, $sce);

            $scope.currentView = viewService.data.currentView;
            $scope.viewOptions = {
                views: viewService.data.allViews,
                selected: viewService.data.currentView.id
            };
            $scope.projectKey = projectKey;
            $scope.columns = columnService.data.currentColumns;
            $scope.allColumns = columnService.data.allColumns;
            $scope.issues = searchService.data.issues;
            $scope.issuesCount = searchService.data.issuesCount;
            $scope.pages = [];

            $scope.loading = true;

            var normalizeColumnIds = function(columnsObject) {
                var columnIds = [];
                if (angular.isArray(columnsObject)) {
                    columnIds = columnsObject.map(function(columnId) {
                        return parseInt(columnId);
                    });
                } else if (angular.isDefined(columnsObject)) {
                    columnIds.push(parseInt(columnsObject));
                }
                return columnIds;
            };


            var $viewsQ = viewService.getViews({viewId: viewId}).then(function() {
                $scope.viewOptions.selected = viewService.data.currentView.id;
                var currentViewIdDefined = angular.isDefined(viewService.data.currentView.id);
                if ($scope.isGadget && currentViewIdDefined) {
                    AP.require('jira', function (jira) {
                        jira.setDashboardItemTitle(viewService.data.currentView.name);
                    });
                }
                $scope.showDeleteButton = currentViewIdDefined;
                if ($routeParams.columnId) {
                    $scope.currentView.columns = normalizeColumnIds($routeParams.columnId);
                }
                $scope.showSaveButton = ($scope.currentView._origColumns || $scope.currentView.columns.length > 0) && !angular.equals($scope.currentView.columns, $scope.currentView._origColumns);
                columnService.getColumns({columnIds: $scope.currentView.columns});
            });

            var searchOptions = {
                filterId: $routeParams.filterId || searchService.data.filterId || filterId,
                projectKey: $scope.projectKey || '',
                maxResults: 50,
                startAt: $routeParams.startAt || searchService.data.issuesCount.startAt || 0
            };
            var $issuesQ = searchService.getIssues(searchOptions);

            $q.all([$viewsQ, $issuesQ])
                .then(function(data) {
                    $scope.loading = false;
                    $scope.issuesCount.end = Math.min($scope.issuesCount.startAt + $scope.issuesCount.maxResults, $scope.issuesCount.total);
                    var pagesNum = Math.ceil($scope.issuesCount.total / $scope.issuesCount.maxResults);
                    for (var i = 0; i < pagesNum; i++) {
                      $scope.pages.push(i);
                    }
                    AP.resize();
                })
                .catch(function(data, status){
                    $scope.loading = false;
                });

            $scope.filterOptions = {
                filters: searchService.data.allFilters
            };
            var defaultFilter = searchService.getDefaultFilter($scope.projectKey);
            searchService.getFilters(loggedInUser, {}, function(filters) {
                filters.splice(0, 0, defaultFilter); // init first time only
            }).then(function(filters) {
                $scope.filterOptions.selected = searchOptions.filterId;
            });

            $scope.viewSelected = function(viewId) {
                $location.search({viewId: viewId});
            };

            $scope.filterSelected = function() {
                var search = $location.search();
                search.filterId = $scope.filterOptions.selected;
                $location.search(search);
            };

            $scope.setStartAt = function(startAt) {
                var search = $location.search();
                search.startAt = startAt;
                $location.search(search);
            };

            $scope.refresh = function() {
                searchService.resetState();
                columnService.resetState();
                viewService.resetState();
                $route.reload();
            };

            $scope.addColumn = function() {
                var modalInstance = $modal.open({
                    templateUrl: 'column-dialog-template',
                    controller: 'ColumnDialogController',
                    resolve: {
                        columns: function () {
                            return columnService.getAllColumnsGrouped();
                        },
                        selectedColumnIds: function () {
                            return $scope.currentView.columns;
                        }
                    }
                });
                modalInstance.result.then(function (columns) {
                    var search = $location.search();
                    if (columns) {
                        search.columnId = columns;
                        if (columns.length == 0) {
                            // force route reload
                            search._ = Date.now();
                        }
                        $location.search(search);
                    }
                });
            };

            $scope.updateColumns = function() {
                var columns = $scope.currentView.columns;
                var search = $location.search();
                if (columns) {
                    search.columnId = columns;
                    if (columns.length == 0) {
                        // force route reload
                        search._ = Date.now();
                    }
                    $location.search(search);
                }
            };

            $scope.saveViewAsNew = function() {
                viewService.addView($scope.currentView).then(function(view) {
                    var search = $location.search();
                    search.viewId = view.id;
                    $location.search(search);
                });
            };

            $scope.saveView = function() {
                if ($scope.currentView.id) {
                    viewService.updateView($scope.currentView);
                    $scope.showSaveAsNewButton = false;
                    $scope.showSaveButton = false;
                } else {
                    $scope.saveViewAsNew();
                }
            };

            $scope.deleteView = function() {
                viewService.removeView($scope.currentView).then(function() {
                    var search = $location.search();
                    if (!search.viewId) {
                        // force route reload
                        search._ = Date.now();
                    } else {
                        delete search.viewId;
                    }
                    $location.search(search);
                });
            };

            $scope.viewNameChanged = function() {
                $scope.showSaveAsNewButton = angular.isDefined($scope.currentView.id);
                $scope.showSaveButton = true;
            };

            $scope.dragDropOptions = {
                orderChanged: function (event) {
                    $scope.showSaveButton = true;
                    var search = $location.search();
                    search.columnId = columnService.getColumnIds($scope.columns);
                    $location.search(search);
                },
                //containment: "#dragArea",
                dragStart: function(event) {
                    angular.element($document[0].body).addClass('as-sortable-unselectable');
                    angular.element('.sortable-placeholder').css("min-width", event.source.itemScope.element.css("width"));
                },
                dragCancel: function(event) {
                    angular.element($document[0].body).removeClass('as-sortable-unselectable');
                }
            };
        }
    }
]);
