function prettyHours(input) {
    var value = parseInt(input) || 0;
    return value != 0 ? Math.round(value / 36) / 100 + "h" : "";
}
app.filter('prettyHours', function() {
    return prettyHours;
});
app.filter('prettyDuration', function() {
    return function(input) {
        var value = parseInt(input) || 0;
        var secondsInMinute = 60;
        var secondsInHour = 60 * secondsInMinute;
        var secondsInDay = 8 * secondsInHour;
        var secondsInWeek = 5 * secondsInDay;
        var secondsInMonth = 30 * secondsInDay;
        var secondsInYear = 12 * secondsInMonth;
        var result = "";
        if (value >= secondsInYear) {
            var years = Math.floor(value / secondsInYear);
            result += years + " years ";
            value -= years * secondsInYear;
        }
        if (value >= secondsInMonth) {
            var months = Math.floor(value / secondsInMonth);
            result += months + " months ";
            value -= months * secondsInMonth;
        }
        if (value >= secondsInWeek) {
            var weeks = Math.floor(value / secondsInWeek);
            result += weeks + " weeks ";
            value -= weeks * secondsInWeek;
        }
        if (value >= secondsInDay) {
            var days = Math.floor(value / secondsInDay);
            result += days + " days ";
            value -= days * secondsInDay;
        }
        if (value >= secondsInHour) {
            var hours = Math.floor(value / secondsInHour);
            result += hours + " hours ";
            value -= hours * secondsInHour;
        }
        if (!result && value >= secondsInMinute) {
            var minutes = Math.floor(value / secondsInMinute);
            result += minutes + " minutes";
            value -= minutes * secondsInMinute;
        }
        return result;
    }
});
app.filter('timeago', [function() {
    return function(input) {
        var value = moment(input); // iso date string
        return value.isValid() ? value.fromNow() : input;
    }
}]);
app.filter('timeToResolve', ['applicationLoggingService', function(logger) {
    return function(issue) {
        if (!issue) {
            return "";
        }
        var resolution = issue.fields.resolution;
        var resolutiondate = issue.fields.resolutiondate;
        if (resolutiondate) {
            resolutiondate = moment(resolutiondate);
        } else {
            resolutiondate = moment();
        }
        logger.debug('resolutiondate', resolutiondate.toDate());
        var created = moment(issue.fields.created);
        logger.debug('created', created.toDate());
        var result = "";
        var diff = moment.unix(resolutiondate.unix() - created.unix());
        diff.add(diff.zone() / 60, 'hours');
        logger.debug('diff', diff.toDate());
        var years = diff.years() - 1970;
        if (years > 0) {
            result += years + "y ";
        }
        var months = diff.months();
        if (months > 0) {
            result += months + "m ";
        }
        var days = diff.date() - 1;
        if (days >= 7) {
            var weeks = Math.floor(days / 7);
            result += weeks + "w ";
            days -= weeks * 7;
        }
        if (days > 0) {
            result += days + "d ";
        }
        var hours = diff.hours();
        if (hours > 0) {
            result += hours + "h ";
        }
        var minutes = diff.minutes();
        if (!result) {
            result += minutes + " min";
        }
        logger.debug('timeToResolve', result);
        return result;
    }
}]);
app.filter('timeToResolveLozenge', function() {
    return function(issue) {
        if (!issue) {
            return "";
        }
        var result = "aui-lozenge "
        var resolution = issue.fields.resolution;
        var resolutiondate = issue.fields.resolutiondate;
        if (!resolutiondate) {
            result += "aui-lozenge-subtle ";
            resolutiondate = moment();
        } else {
            resolutiondate = moment(resolutiondate);
        }
        var created = moment(issue.fields.created);
        var diff = moment.unix(resolutiondate.unix() - created.unix());
        diff.add(diff.zone() / 60, 'hours');
        var years = diff.years() - 1970;
        var months = diff.months();
        var days = diff.date() - 1;
        var hours = diff.hours();
        var minutes = diff.minutes();
        if (years > 0 || months > 0 || days > 5) {
            result += "aui-lozenge-error";
        } else if (days > 2 || (days == 2 && (hours > 0 || minutes > 0))) {
            result += "aui-lozenge-current";
        } else {
            result += "aui-lozenge-success";            
        }
        return result;
    }
});
app.filter('timeLeft', function() {
    return function(issue) {
        if (!issue) {
            return "";
        }
        var duedate = issue.fields.duedate;
        if (!duedate && issue.fields.timeestimate) {
            return prettyHours(issue.fields.timeestimate);
        } else if (!duedate){
            return "-";
        }
        duedate = moment(duedate);

        var now = moment();
        var diff = null;
        var result = "";
        if (duedate.isBefore(now)) {
            result += "-";
            diff = now.subtract(duedate.unix() * 1000);
        } else {
            diff = duedate.subtract(now.unix() * 1000);
        }
        diff.add(diff.zone() / 60, 'hours');
        var years = diff.years() - 1970;
        if (years > 0) {
            result += years + "y ";
        }
        var months = diff.months();
        if (months > 0) {
            result += months + "m ";
        }
        var days = diff.date() - 1;
        if (days >= 7) {
            var weeks = Math.floor(days / 7);
            result += weeks + "w ";
            days -= weeks * 7;
        }
        if (days > 0) {
            result += days + "d ";
        }
        var hours = diff.hours();
        if (hours > 0) {
            result += hours + "h ";
        }
        var minutes = diff.minutes();
        if (!result) {
            result += minutes + " min";
        }
        return result;
    }
});
app.filter('timeLeftLozenge', function() {
    return function(issue) {
        if (!issue) {
            return "";
        }
        var result = "aui-lozenge "
        var duedate = issue.fields.duedate;
        var timeestimate = issue.fields.timeestimate;
        var now = moment();
        if (duedate) {
            duedate = moment(duedate);
        } else {
            result += "aui-lozenge-subtle ";
            duedate = now;
        }
        var diff = null;
        var timeLeft = null;
        if (duedate.isBefore(now)) {
            diff = now.subtract(duedate.unix() * 1000);
            timeLeft = -diff.unix();
        } else {
            diff = duedate.subtract(now.unix() * 1000);
            timeLeft = diff.unix();
        }
        diff.add(diff.zone() / 60, 'hours');
        var years = diff.years() - 1970;
        var months = diff.months();
        var days = diff.date() - 1;
        var hours = diff.hours();
        var minutes = diff.minutes();
        var minWork = 8 * 60 * 60; // one day = 8 hours in seconds
        if (timeLeft && timeestimate > timeLeft) {
            result += "aui-lozenge-error";
        } else if ((!timeestimate && timeLeft < minWork)
            || (timeestimate && timeLeft && (timeLeft - timeestimate < minWork))
            || (!timeestimate && !timeLeft)) {
            result += "aui-lozenge-current";
        } else {
            result += "aui-lozenge-success";            
        }
        return result;
    }
});
